import { render } from "@testing-library/react";
import React, { Component, useState } from "react";
import "../styles/experience.css";
import WorkExperienceComponent from "./sub-components/WorkExperienceComponent";
import add from "../images/addComponent.png";
import AddComponentButton from "./addComponentButton";

const WorkExperience = (props) => {
  const [components, setComponents] = useState(["Work Experience"]);

  const _addComponent = () => {
    setComponents(components.concat("Work Experience"));
  };

  return (
    <div className="work-experience-container">
      <h3 className="work-experience-header">
        Work Experience
        {!props.complete ? (
          <AddComponentButton
            click={_addComponent}
            class="add-component-button"
          />
        ) : null}
      </h3>
      {components.map((elem) => (
        <WorkExperienceComponent complete={props.complete} />
      ))}
    </div>
  );
};

export default WorkExperience;
