import React, { Component, useState } from "react";
import remove from "../../images/remove.png";
import Lorem from "../Lorem";
import "../../styles/experience.css";
import Info from "./Info";
import WorkBullets from "./WorkBullets";

const WorkComponentClickable = (props) => {
  const [clicked, setClicked] = useState(false);
  const [title, setTitle] = useState(props.title);
  const [company, setCompany] = useState(props.company);
  const [date, setDate] = useState(props.date);

  const _handleClick = (e) => {
    e.target.parentNode.remove();
  };

  return (
    <div className="work-experience-component-container">
      {!props.complete ? (
        <img
          src={remove}
          className="remove-button"
          onClick={_handleClick}
          alt="remove"
        ></img>
      ) : null}
      <Info tag="h4" info={title} complete={props.complete} />
      <Info tag="h5" info={company} complete={props.complete} />
      <Info tag="h6" info={date} complete={props.complete} />
      <WorkBullets
        defaultEntry={Lorem.generateSentences(1)}
        complete={props.complete}
      />
    </div>
  );
};

