import "./App.css";
import "./styles/container.css";
import React, { Component, useState } from "react";
import Header from "./components/header";
import PersonalInfo from "./components/personal-info";
import ContactBar from "./components/ContactBar";
import WorkExperience from "./components/WorkExperience";
import Skills from "./components/Skills";
import Education from "./components/Education";
import { render } from "@testing-library/react";
import SubmitButton from "./components/SubmitButton";


const App = () => {
  const [complete, setComplete] = useState(false);

  const _completeCV = () => {
    setComplete(true);
  };

  const _editCV = () => {
    setComplete(false);
  };

  return (
    <div className="App">
      <Header />
      <div id="cv-container">
        <PersonalInfo complete={complete} />
        <ContactBar complete={complete} />
        <div className="experience-container">
          <div className="left-container">
            <WorkExperience complete={complete} />
          </div>
          <div className="right-container">
            <Skills complete={complete} />
            <Education complete={complete} />
          </div>
        </div>
        {!complete ? (
          <SubmitButton clicked={_completeCV} type="Complete" />
        ) : (
          <SubmitButton clicked={_editCV} type="Edit" />
        )}
      </div>
    </div>
  );
};

export default App;
