import React, { Component, useState, useEffect } from "react";
import "../../styles/personal-info.css";

const PersonalInfoComponent = (props) => {
  const [clicked, setClicked] = useState(false);
  const [placeHolder, setPlaceHolder] = useState(props.placeHolder);
  const elemType = props.elemType;
  const className = props.type;

  const _handleClick = (e) => {
    e.preventDefault();
    setClicked(true);
  };

  const _handleChange = (e) => {
    setPlaceHolder(e.target.value);
  };

  const _handleSubmit = (e) => {
    setClicked(false);
  };

  const _handleBlur = (e) => {
    setClicked(false);
  };

  if (!props.complete) {
    if (!clicked) {
      return (
        <props.elemType onDoubleClick={_handleClick} className={className}>
          {placeHolder}
        </props.elemType>
      );
    } else {
      return (
        <form className={className} onSubmit={_handleSubmit}>
          <input
            type="text"
            className={className}
            onChange={_handleChange}
            autoFocus
            onBlur={_handleBlur}
          ></input>
        </form>
      );
    }
  } else {
    return <props.elemType className={className}>{placeHolder}</props.elemType>;
  }
};

